body {
  height: 100%;
}
.recipe__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    grid-auto-rows: 33rem;
}
.recipe__item {
    display: grid;
    grid-template-rows: 1fr 3fr 3fr;
    /* grid-template-columns: 1fr 2fr; */
    grid-template-areas: "title""glass""description";
    color: var(--text-color);
    font-size: 2rem;
    background-color: var(--card-color);
    padding: var(--padding);
    margin: 0.5rem;
    cursor: pointer;
    /* grid-gap: 1rem; */
}

.recipe__item__title {
    grid-area: title;
    font-size: 2.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    color: var(--title-text-color);
    /* justify-content: center; */
}

.recipe__item__glass {
    grid-area: glass;
    display: flex;
    align-items: center;
    justify-content: center;
}

.recipe__item__description {
    grid-area: description;
    display: flex;
    align-items: left;
    font-size: 1.25rem;
    overflow: hidden;
}

.recipe__item:hover {
    background-color: var(--card-color-hover);
}
.glass {
    height: 10rem;
    width: 10rem;
}
.dialog {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}

.dialog__container {
    /* border: 0.2rem solid white; */
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    grid-template-areas: "message message""dismiss confirm";
    background-color: var(--dialog-color);
}

@media (max-width: 800px) {
    .dialog {
        align-items: flex-end;
    }

    .dialog__container {
        flex: 1 1;
    }
}

.dialog__message {
    grid-area: message;
    color: var(--text-color);
    font-size: 2rem;
    padding: var(--padding);
    display: flex;
    justify-content: center;
}

.dialog__dismiss {
    grid-area: dismiss;
    display: flex;
    justify-content: flex-start;
    padding: var(--padding);
}

.dialog__confirm {
    grid-area: confirm;
    display: flex;
    justify-content: flex-end;
    padding: var(--padding);
}

.dialog-active {
    visibility: visible;
}
.button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    border: none;
    cursor: pointer;
    padding: var(--padding);
}

.button-action {
    background-color: var(--button-action-color);
    color: var(--button-action-text-color);
}

.button-default {
    background-color: var(--button-default-color);
    color: var(--button-default-text-color);
}

.button:hover {
    -webkit-filter: brightness(85%);
            filter: brightness(85%);
}
.processing {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    top: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}

.processing__container {
    color: white;
    font-size: 2rem;
    border: 0.2rem solid white;
}

.processing-active {
    visibility: visible;
}
.input {
    display: flex;
    border: 0;
    color: var(--input-color);
    font-size: 2rem;
    font-family: var(--font-family);
    padding: var(--input-padding);
}
.recipe-edit {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: auto;
    grid-gap: 1rem;
    padding: 1rem;
}
.select {
    display: flex;
    border: 0;
    color: var(--input-color);
    font-size: 2rem;
    font-family: var(--font-family);
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    padding: var(--input-padding);
}
.input-container {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    font-family: var(--font-family);
}

.input-container__label {
    color: white;
    font-size: 1.5rem;
}
/* roboto-condensed-regular - latin_latin-ext */
@font-face {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 400;
  src: url('/assets/roboto-condensed-v16-latin_latin-ext-regular.eot');
  /* IE9 Compat Modes */
  src: local('Roboto Condensed'), local('RobotoCondensed-Regular'),
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.woff') format('woff'),
    /* Modern Browsers */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('/assets/roboto-condensed-v16-latin_latin-ext-regular.svg#RobotoCondensed') format('svg');
  /* Legacy iOS */
}

/* roboto-condensed-700 - latin_latin-ext */
