    /* Modern Browsers */
    url('/assets/roboto-condensed-v16-latin_latin-ext-700.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('/assets/roboto-condensed-v16-latin_latin-ext-700.svg#RobotoCondensed') format('svg');
  /* Legacy iOS */
}
